import Vue from 'vue'
import Router from 'vue-router'
import Level1 from "@/pages/Level1"
import Level2 from "@/pages/Level2"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Level1',
      component: Level1
    },
    {
      path:"/level2",
      name:"Level2",
      component:Level2
    }
  ]
})



