// 热区自适应
function changeCoords(pw,img,map){
    var w1 = img.offsetWidth;
    var percent = w1/pw;
    Array.from(map.children).forEach((item)=>{
        console.log(item.nodeName)
        if(item.nodeName==="AREA"){
          var coords = item.getAttribute("coords");
          var arr = coords.split(",");
          for(var i=0;i<arr.length;i++){
              arr[i] *= percent;
          }
          var str = arr.join(",");
          item.setAttribute("coords",str);
        }

    })
}
// 判断数组是否有重复项
function isRepeat(arr){
  var hash  = {}
  for(var i in arr){
    if(hash[arr[i]]){
      return true
    }else{
      hash[arr[i]] = true
    }
  }
  return false
}
export {
  changeCoords,
  isRepeat
}



