<template lang="html">
  <div class="levelBtn">
    <img :src="levelBtn" alt="">
  </div>
</template>

<script>
import levelBtn from "@/assets/imgs/com/01.png"
export default {
  data(){
    return {
      levelBtn
    }
  }
}
</script>

<style lang="css" scoped>
  .levelBtn{
    width: 14rem;
    height:6rem;
    color:white;
    margin: 0 auto;
    background: url(../assets/imgs/com/bg.gif) no-repeat;
    background-size: cover;
    position: absolute;
    left: 50%;
    top:2rem;
    transform: translateX(-50%);
  }
  .levelBtn img{
    width:8rem;
    margin-top:1.5rem;
  }
</style>



