<template lang="html">
  <div class="level1">
    <div class="hotPic">
      <img :src="levelPic" alt="" usemap="#planetmap" ref="img">
      <map  name="planetmap" id="planetmap" ref="map">
        <template v-for="(item,index) in options">
          <area   href="#" :alt="item.text"  :coords="item.coords"  @touchend="toggle($event,item,index)"/>
          <span class="option" v-if="item.isPop" :style="item.styleObj" :ref="'option'+index" @touchend="toggle($event,item,index)">{{item.text}}</span>
        </template>
      </map>
    </div>
    <LevelBtn></LevelBtn>
    <SubmitBtn @click.native="submit"></SubmitBtn>
  </div>
</template>

<script>
import LevelBtn from "@/components/LevelBtn"
import SubmitBtn from "@/components/SubmitBtn"
import { changeCoords,isRepeat } from "@/assets/JS/tools.js"
import levelPic from "@/assets/imgs/01.png"
export default {
  components:{
    LevelBtn,
    SubmitBtn
  },
  data(){
    return {
      options:[
        {text:"帽子佩戴不正确",coords:"245,122,342,183",isAnswer:true,isPop:false,styleObj:{left:0,top:0}},
        {text:"赤膊",coords:"253,249,360,421",isAnswer:false,isPop:false,styleObj:{left:0,top:0}},
        {text:"抽烟",coords:"185,222,232,282",isAnswer:true,isPop:false,styleObj:{left:0,top:0}},
        {text:"拖鞋",coords:"215,654,274,732",isAnswer:true,isPop:false,styleObj:{left:0,top:0}},
        // {text:"拖鞋",coords:"307,663,352,748",isAnswer:true,isPop:false,styleObj:{left:0,top:0}},
      ],
      correctAnswer:['帽子佩戴不正确','赤膊','抽烟','拖鞋'],
      // 选出的答案和正确答案做对比
      answerContainer:[],
      levelPic
    }
  },
  methods:{
    // 可以提出来
    submit(){
      if(this.answerContainer.length!==this.correctAnswer.length||isRepeat(this.answerContainer)){
        // 去往错误提示页面
        console.log("err")
      }else if(this.answerContainer.every((item)=>{ return this.correctAnswer.includes(item)})){
          this.$router.replace({path:"/level2"})
          // 进入下一关
          console.log("correct")
        }
    },
    // 可以提出来
    toggle(event,item,index){
      item.isPop = !item.isPop
      // DOM是异步渲染的
      this.$nextTick(()=>{
        if(item.isPop){
          item.styleObj.left = (event.changedTouches[0].clientX-this.$refs["option"+index][0].offsetWidth/2)+'px'
          item.styleObj.top = (event.changedTouches[0].clientY-this.$refs["option"+index][0].offsetHeight/2)+"px"
          this.answerContainer.push(item.text)
        }else{
          this.answerContainer.splice(this.answerContainer.indexOf(item.text),1)
        }
      })
    }
  },
  mounted(){
    var map = this.$refs.map;
    var img = this.$refs.img
    changeCoords(580,img,map)
  }
}
</script>

<style lang="css" scoped>
  .hotPic{
    font-size: 16px;
    color:white;
  }
  .hotPic img{
    width: 34rem;
    display:block;
    position: absolute;
    left:50%;
    top:5rem;
    transform: translateX(-50%);
  }
  area{
    text-decoration: none;
  }
  span{
    background: rgba(0, 0, 0, 0.52);
    padding: 0.3rem 0.6rem;
    border-radius:.5rem;
  }
  span::before{
    content: "√";
    color:#00fd00;
    margin-right: .4rem;
    font-size: 14px;
    font-weight: 700;
  }
  .option{
    position:fixed;
  }
</style>



